<template>
  <div>
    <div class="message-section">
      <div class="item-name"></div>
      <p>2 </p>
      <div class="basic-use">
        <el-row>
          <el-button :plain="true" @click="open"></el-button>
          <el-button :plain="true" @click="openVn">VNode</el-button>
        </el-row>
      </div>
    </div>

    <div class="message-section">
      <div class="item-name"></div>
      <p></p>
      <div class="basic-use">
        <el-row>
          <el-button :plain="true" @click="open2"></el-button>
          <el-button :plain="true" @click="open3"></el-button>
          <el-button :plain="true" @click="open1"></el-button>
          <el-button :plain="true" @click="open4"></el-button>
        </el-row>
      </div>
    </div>

    <div class="message-section">
      <div class="item-name"></div>
      <p></p>
      <div class="basic-use">
        <el-row>
          <el-button :plain="true" @click="open5"></el-button>
          <el-button :plain="true" @click="open6"></el-button>
          <el-button :plain="true" @click="open7"></el-button>
          <el-button :plain="true" @click="open8"></el-button>
        </el-row>
      </div>
    </div>

    <div class="message-section">
      <div class="item-name"></div>
      <p> center </p>
      <div class="basic-use">
        <el-row>
          <el-button :plain="true" @click="openCenter"></el-button>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    open() {
      this.$message("");
    },

    openVn() {
      const h = this.$createElement;
      this.$message({
        message: h("p", null, [
          h("span", null, " "),
          h("i", { style: "color: teal" }, "VNode")
        ]),
        duration: 2000
      });
    },

    open1() {
      this.$message("");
    },
    open2() {
      this.$message({
        message: "",
        type: "success"
      });
    },

    open3() {
      this.$message({
        message: "",
        type: "warning"
      });
    },

    open4() {
      this.$message.error("");
    },

    open5() {
      this.$message({
        showClose: true,
        message: ""
      });
    },

    open6() {
      this.$message({
        showClose: true,
        message: "",
        type: "success"
      });
    },

    open7() {
      this.$message({
        showClose: true,
        message: "",
        type: "warning"
      });
    },

    open8() {
      this.$message({
        showClose: true,
        message: "",
        type: "error"
      });
    },

    openCenter() {
      this.$message({
        message: "",
        center: true
      });
    }
  }
};
</script>

