<template>
  <div>
    <div class="tag-section">
      <div class="item-name"></div>
      <div class="basic-use">
        <el-row>
          <el-tag></el-tag>
          <el-tag type="success"></el-tag>
          <el-tag type="info"></el-tag>
          <el-tag type="warning"></el-tag>
          <el-tag type="danger"></el-tag>
        </el-row>
      </div>
    </div>

    <div class="tag-section">
      <div class="item-name"></div>
      <div class="basic-use">
        <el-row>
          <el-tag v-for="tag in tags" :key="tag.name" closable :type="tag.type">{{tag.name}}</el-tag>
        </el-row>
      </div>
    </div>

    <div class="tag-section">
      <div class="item-name"></div>
      <p> close </p>
      <div class="basic-use">
        <el-row>
          <el-tag
            :key="tag"
            v-for="tag in dynamicTags"
            closable
            :disable-transitions="false"
            @close="handleClose(tag)"
          >{{tag}}</el-tag>
          <el-input
            class="input-new-tag"
            v-if="inputVisible"
            v-model="inputValue"
            ref="saveTagInput"
            size="small"
            @keyup.enter.native="handleInputConfirm"
            @blur="handleInputConfirm"
          ></el-input>
          <el-button v-else class="button-new-tag" size="small" @click="showInput">+ New Tag</el-button>
        </el-row>
      </div>
    </div>

    <div class="tag-section">
      <div class="item-name"></div>
      <p>Tag </p>
      <div class="basic-use">
        <el-row>
          <el-tag closable></el-tag>
          <el-tag size="medium" closable></el-tag>
          <el-tag size="small" closable></el-tag>
          <el-tag size="mini" closable></el-tag>
        </el-row>
      </div>
    </div>

    <div class="tag-section">
      <div class="item-name"></div>
      <p>Tag darklight  plain</p>
      <div class="basic-use">
        <el-row>
          <el-tag
            v-for="item in items"
            :key="item.label"
            :type="item.type"
            effect="dark"
          >{{ item.label }}</el-tag>
        </el-row>
        <el-row>
          <el-tag
            v-for="item in items"
            :key="item.label"
            :type="item.type"
            effect="plain"
          >{{ item.label }}</el-tag>
        </el-row>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tags: [
        { name: "", type: "" },
        { name: "", type: "success" },
        { name: "", type: "info" },
        { name: "", type: "warning" },
        { name: "", type: "danger" }
      ],
      items: [
        { type: "", label: "" },
        { type: "success", label: "" },
        { type: "info", label: "" },
        { type: "danger", label: "" },
        { type: "warning", label: "" }
      ],
      dynamicTags: ["", "", ""],
      inputVisible: false,
      inputValue: ""
    };
  },
  methods: {
    handleClose(tag) {
      this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
    },

    /* eslint-disable */
    showInput() {
      this.inputVisible = true;
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus();
      });
    },
    /* eslint-disable */

    handleInputConfirm() {
      let inputValue = this.inputValue;
      if (inputValue) {
        this.dynamicTags.push(inputValue);
      }
      this.inputVisible = false;
      this.inputValue = "";
    }
  }
};
</script>

